.text-s1 {
  letter-spacing: normal;
  font-weight: 700;
  font-size: 6rem;
  text-transform: none;
  margin: 0;
  color: #fff;
  margin-left: 10%;
}

.text-s1-sub {
  letter-spacing: normal;
  margin: 0;
  color: #12c7d0;
  margin-left: 10%;
  font-weight: 700;
}

.text-s1-2 {
  letter-spacing: normal;
  font-weight: 1000;
  font-size: 30px;
  text-transform: none;
  margin: 0;
  color: #fff;
  margin-left: 10%;
}

.image-border {
  display: block;
  border: 1px solid black;
  background-image: url("../images/Pachamamapp.png");
  width: 800px;
  height: 500px;
  background-repeat: no-repeat;
  float: right;
  margin-right: 5%;
}

.text-focus-in {
  -webkit-animation: slideInLeft 1.3s;

  animation: slideInLeft 1.3s;
}

.text-focus-in-2 {
  -webkit-animation: slideInLeft 1.4s;

  animation: slideInLeft 1.4s;
}

.text-focus-in-3 {
  -webkit-animation: slideInLeft 1.5s;

  animation: slideInLeft 1.5s;
}
.text-focus-end {
  -webkit-animation: slideInRight 1s, text-focus 1.5s;

  animation: slideInRight 1s, text-focus 1.5s;
}

.text-focus-offRigth {
  -webkit-animation: text-focus 1.5s, slideOffRight 1s;
  animation: text-focus 1.5, slideOffRight 1s;
}

.text-focus-offLeft {
  -webkit-animation: text-focus 1s, slideOffLeft 1.3s;
  animation: text-focus 1s, slideOffLeft 1.3s;
}
.text-focus-offLeft-2 {
  -webkit-animation: text-focus 1s, slideOffLeft 1.4s;
  animation: text-focus 1s, slideOffLeft 1.4s;
}
.text-focus-offLeft-3 {
  -webkit-animation: text-focus 1s, slideOffLeft 1.5s;
  animation: text-focus 1s, slideOffLeft 1.5s;
}

hr {
  border: 3px solid #666;
  border-radius: 300px/10px;
  height: 0px;
  text-align: center;
}

@keyframes slideInLeft {
  0% {
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
    visibility: visible;
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1;
  }
}

@keyframes slideOffLeft {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    visibility: visible;
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1;
  }
}

@keyframes slideInRight {
  0% {
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
    visibility: visible;
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1;
  }
}

@keyframes slideOffRight {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    visibility: visible;
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1;
  }
}

.img {
  background-image: url("../images/fff.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
